.opcb-ui-namespace label {
  margin-bottom: 5px;
  font-weight: bold;
}

.opcb-ui-namespace a {
  color: var(--opcb-bs-blue);
}

.opcb-ui-namespace a:hover {
  color: var(--opcb-bs-dark-blue);
}

.opcb-ui-namespace h1, .opcb-ui-namespace .h1 {
  color: var(--opcb-bs-green);
  text-transform: uppercase;
  text-align: center;
  margin: 0 0 20px;
  font-size: 30px;
  font-weight: normal;
}

.opcb-ui-namespace h2, .opcb-ui-namespace .h2 {
  color: var(--opcb-bs-green);
  text-transform: uppercase;
  margin: 0 0 20px;
  font-size: 24px;
  font-weight: normal;
}

.opcb-ui-namespace h4, .opcb-ui-namespace .h4 {
  color: var(--opcb-bs-green);
  text-transform: uppercase;
  margin: 20px 0;
  padding-bottom: 4px;
  font-size: 18px;
  font-weight: normal;
}

.opcb-ui-namespace .form-help-description {
  color: #666;
  font-size: 12px;
  font-style: italic;
}

.opcb-ui-namespace input::placeholder {
  color: var(--opcb-bs-placeholder-text-color) !important;
}

.opcb-ui-namespace .btn:focus {
  box-shadow: none;
}

.opcb-ui-namespace .btn-primary {
  background-color: var(--opcb-bs-blue);
  border-color: var(--opcb-bs-blue);
}

.opcb-ui-namespace .btn-primary:hover {
  background-color: var(--opcb-bs-dark-blue);
}

.opcb-ui-namespace .btn-link {
  color: var(--opcb-bs-blue);
  padding: 0;
  font-size: 15px;
  text-decoration: none;
}

.opcb-ui-namespace .btn-link:hover {
  color: var(--opcb-bs-blue);
  text-decoration: underline;
}

.opcb-ui-namespace .btn-light {
  border: .5px solid var(--opcb-bs-blue);
  color: var(--opcb-bs-blue);
  padding: 5px 10px;
  font-size: 14px;
}

.opcb-ui-namespace .btn-light:hover {
  border: .5px solid var(--opcb-bs-blue);
  color: var(--opcb-bs-dark-blue);
}

.opcb-ui-namespace .btn-light:focus {
  border: .5px solid var(--opcb-bs-blue);
}

.opcb-ui-namespace form .form-control, .opcb-ui-namespace form .form-select {
  font-size: 14px;
}

.opcb-ui-namespace form .info-icon {
  float: right;
  cursor: pointer;
}

.opcb-ui-namespace header {
  background-color: var(--opcb-bs-gray-100);
  margin-bottom: 20px;
}

.opcb-ui-namespace header .header-inner {
  max-width: 1220px;
  justify-content: space-between;
  align-items: center;
  margin: 0 auto;
  padding: 20px;
  display: flex;
}

.opcb-ui-namespace header .header-inner #title {
  color: var(--opcb-bs-dark);
  text-transform: uppercase;
  max-width: 200px;
  margin-right: 17px;
  font-size: 18px;
  font-weight: normal;
  text-decoration: none;
  display: inline-block;
  position: relative;
  top: -.5px;
}

.opcb-ui-namespace header .header-inner #title .device-model-name {
  min-width: 100px;
  font-size: 10px;
  position: absolute;
  top: 24px;
  left: 0;
}

.opcb-ui-namespace header .header-inner #logo {
  margin-right: 10px;
  display: block;
  position: relative;
  top: -3px;
}

.opcb-ui-namespace header .header-inner #logo img {
  max-width: 80px;
}

.opcb-ui-namespace header .header-inner #menu-container #main-menu li {
  position: relative;
}

.opcb-ui-namespace header .header-inner #menu-container #main-menu li .menu-item {
