@import "~bootstrap/scss/mixins/banner";
@include bsBanner("");

// scss-docs-start import-stack
// Configuration
@import "~bootstrap/scss/functions";
$prefix: opcb-bs-;
@import "~bootstrap/scss/variables";
@import "~bootstrap/scss/maps";
@import "~bootstrap/scss/mixins";
@import "~bootstrap/scss/utilities";

@import "~bootstrap/scss/root";

body {
    margin: 0;
}

.opcb-ui-namespace {
    margin: 0; // 1
    font-family: var(--#{$prefix}body-font-family);
    @include font-size(var(--#{$prefix}body-font-size));
    font-weight: var(--#{$prefix}body-font-weight);
    line-height: var(--#{$prefix}body-line-height);
    color: var(--#{$prefix}body-color);
    text-align: var(--#{$prefix}body-text-align);
    background-color: var(--#{$prefix}body-bg); // 2
    -webkit-text-size-adjust: 100%; // 3
    -webkit-tap-highlight-color: rgba($black, 0); // 4
    font-size: 14px;
}

:root {
    --opcb-bs-blue: #1388b7;
    --opcb-bs-dark-blue: #0f688b;
    --opcb-bs-dark: #404040;
    --opcb-bs-border-color: #e4e4e4;
    --opcb-bs-help-text-color: #737373;
    --opcb-bs-grey: #e8e8e8;
    --opcb-bs-green: #60b49f;
    --opcb-bs-purple: #db57ca;
    --opcb-bs-yellow: #ffc108;
    --opcb-bs-red: #f3513f;
    --opcb-bs-useless-text-color: #888;
    --opcb-bs-placeholder-text-color: #b5b2b2;
    --opcb-bs-warning-rgb: 248, 171, 4;
}

$bootstrap-icons-font: "bootstrap-icons" !default;
$bootstrap-icons-font-dir: "npm:bootstrap-icons/font/fonts" !default;
$bootstrap-icons-font-file: #{$bootstrap-icons-font-dir}/#{$bootstrap-icons-font} !default;
$bootstrap-icons-font-hash: "8d200481aa7f02a2d63a331fc782cfaf" !default;
$bootstrap-icons-font-src: url("#{$bootstrap-icons-font-file}.woff2") format("woff2"),
    url("#{$bootstrap-icons-font-file}.woff") format("woff") !default;

@font-face {
    font-display: block;
    font-family: $bootstrap-icons-font;
    src: $bootstrap-icons-font-src;
}

.opcb-ui-namespace {
    @import "~bootstrap/scss/bootstrap";
    @import "~bootstrap-icons/font/bootstrap-icons";

    position: relative;
    padding-bottom: 10px;

    /* tags */
    label {
        margin-bottom: 5px;
        font-weight: bold;
    }

    a {
        color: var(--opcb-bs-blue);

        &:hover {
            color: var(--opcb-bs-dark-blue);
        }
    }

    h1 {
        font-size: 30px;
        color: var(--opcb-bs-green);
        margin: 0;
        text-transform: uppercase;
        margin-bottom: 20px;
        font-weight: normal;
        text-align: center;
    }

    h2 {
        font-size: 24px;
        color: var(--opcb-bs-green);
        margin: 0;
        text-transform: uppercase;
        margin-bottom: 20px;
        font-weight: normal;
    }

    h4 {
        color: var(--opcb-bs-green);
        margin: 20px 0;
        font-weight: normal;
        font-size: 18px;
        text-transform: uppercase;
        padding-bottom: 4px;
    }

    .form-help-description {
        font-style: italic;
        font-size: 12px;
        color: #666;
    }

    input::placeholder {
        color: var(--opcb-bs-placeholder-text-color) !important;
    }

    /* bootstrap */

    .btn:focus {
        box-shadow: none;
    }

    .btn-primary {
        background-color: var(--opcb-bs-blue);
        border-color: var(--opcb-bs-blue);
    }

    .btn-primary:hover {
        background-color: var(--opcb-bs-dark-blue);
    }

    .btn-link {
        padding: 0;
        font-size: 15px;
        color: var(--opcb-bs-blue);
        text-decoration: none;

        &:hover {
            text-decoration: underline;
            color: var(--opcb-bs-blue);
        }
    }

    .btn-light {
        border: 0.5px solid var(--opcb-bs-blue);
        color: var(--opcb-bs-blue);
        padding: 5px 10px;
        font-size: 14px;

        &:hover {
            border: 0.5px solid var(--opcb-bs-blue);
            color: var(--opcb-bs-dark-blue);
        }
    }

    .btn-light:focus {
        border: 0.5px solid var(--opcb-bs-blue);
    }

    /* form */

    form {
        & .form-control,
        & .form-select {
            font-size: 14px;
        }

        & .info-icon {
            float: right;
            cursor: pointer;
        }
    }

    /* header */

    header {
        background-color: var(--opcb-bs-gray-100);
        margin-bottom: 20px;

        & .header-inner {
            max-width: 1220px;
            padding: 20px;
            margin: 0 auto;
            display: flex;
            align-items: center;
            justify-content: space-between;
            /* border-bottom: 1px var(--opcb-bs-gray-300) solid; */

            & #title {
                font-size: 18px;
                color: var(--opcb-bs-dark);
                font-weight: normal;
                text-transform: uppercase;
                display: inline-block;
                margin-right: 17px;
                position: relative;
                top: -0.5px;
                max-width: 200px;
                text-decoration: none;

                & .device-model-name {
                    position: absolute;
                    top: 24px;
                    left: 0;
                    font-size: 10px;
                    min-width: 100px;
                }
            }

            & #logo {
                position: relative;
                display: block;
                margin-right: 10px;
                top: -3px;

                & img {
                    max-width: 80px;
                    /* display: inline-block; */
                }
            }

            & #menu-container {
                & #main-menu {
                    & li {
                        position: relative;
                        /* border-right: 0.2px solid var(--opcb-bs-grey); */

                        /* &::after {
                        content: "";
                        display: block;
                        position: absolute;
                        right: -2px;
                        top: 11px;
                        color: #000;
                        font-size: 10px;
                    } */

                        & .menu-item {
                            color: var(--opcb-bs-blue);

                            &.active {
                                color: var(--opcb-bs-dark);
                                pointer-events: none;
                            }
                        }
                    }

                    & :last-child {
                        border: none;
                        &::after {
                            content: none;
                        }
                    }

                    @media (max-width: 800px) {
                        display: none;
                    }
                }

                & #burger-menu {
                    font-size: 22px;
                    display: none;

                    & .btn {
                        padding: 0;
                        /* padding: 5px; */
                        font-size: 25px;
                        color: var(--opcb-bs-dark);
                        border: 0.5px solid #bbb;
                        line-height: 0;
                    }

                    @media (max-width: 800px) {
                        display: block;
                    }
                }

                & #mobile-menu {
                    padding-top: 70px;
                    max-width: 220px;
                    border: none;
                    background-color: rgba(255, 255, 255, 0.9);

                    & .list-group-item {
                        /* font-size: 19px; */
                        background: transparent;
                        border: none;
                        margin: 5px 0;
                    }

                    & .list-group-item:not([class*="active"]) {
                        color: var(--opcb-bs-blue);
                    }

                    & .list-group-item:active {
                        background-color: var(--opcb-bs-blue);
                        color: #fff;
                    }

                    & .list-group-item.active {
                        background-color: var(--opcb-bs-blue);
                        color: #fff;
                    }
                }
            }
        }
    }

    footer {
        border-top: 1px var(--opcb-bs-gray-300) solid;
        max-width: 1220px;
        padding: 20px 20px 0 20px;
        margin: 60px auto;
        font-size: 0.8em;
        display: flex;
        /* justify-content: space-between; */
        column-gap: 30px;
        row-gap: 10px;
        flex-wrap: wrap;

        & hr {
            /* height: 1px; */
            /* background-color: var(--opcb-bs-gray-300); */
            border-top: 1px var(--opcb-bs-gray-600) solid;
            margin: 0 0 10px 0;
            padding: 0;
        }

        & .footer-logo {
            max-width: 80px;
            opacity: 0.5;
            margin-bottom: 10px;
        }

        & p {
            margin-bottom: 4px;
        }

        & .overvis-info {
            color: var(--opcb-bs-useless-text-color);
        }

        & .sep {
            display: inline-block;
            margin: 0 6px;
        }

        & .state {
            & .state-item {
                margin-right: 20px;
                color: var(--opcb-bs-help-text-color);

                & .value {
                    color: var(--opcb-bs-dark);
                }
            }
        }

        & .value {
            color: #333;
        }
    }

    /* form menu */

    .form-menu-container {
        overflow-y: hidden;

        & .nav-menu {
            min-width: 500px;
            margin-bottom: 20px;

            & button:not([class*="active"]) {
                color: var(--opcb-bs-blue);
            }

            & .dropdown-item:active {
                background-color: var(--opcb-bs-blue);
                color: #fff;
            }

            & .dropdown-item.active {
                background-color: var(--opcb-bs-blue);
                color: #fff;
            }
        }
    }

    /* alerts */

    #alerts-container {
        max-width: 800px;
        text-align: center;
        margin: 0 auto;

        & .alert {
            margin: 20px 10px;
            border: none;
        }
    }

    /* #alerts-container {
    max-width: 1220px;
    min-height: 70px;
    margin: 0 auto;
    padding: 0 20px;
    display: flex;
    justify-content: center;
    align-items: center;
    flex-direction: column;

    & .alert {
        margin-bottom: 0;
        padding: 10px;
        height: fit-content;
        width: 100%;

        & .btn-close {
            float: right;
        }
    }
    & #reboot-device-alert {
        display: flex;
        align-items: baseline;
        justify-content: space-between;
    }
} */

    /* content */

    #content {
        max-width: 1220px;
        padding: 0 20px;
        margin: 0 auto;

        & .tab-pane .list-group {
            position: absolute;
            width: 100%;
            z-index: 1000;
            padding-right: 25px;

            & .list-group-item:hover {
                background-color: var(--opcb-bs-grey);
            }
        }
    }

    #state-device-sticker-img-container {
        position: relative;

        & #sticker-container {
            position: relative;
            width: 200px;
            margin: 0 auto;
            top: -95px;
            margin-bottom: -60px;
            box-shadow: 0px 0px 2px 0px rgb(0 0 0 / 30%);

            & img {
                width: 100%;
            }

            @media (max-width: 800px) {
                left: 46%;
                width: 40px;
            }

            @media (max-width: 480px) {
                left: 45%;
                width: 30px;
            }
        }
    }

    #state-content {
        max-width: 1220px;
        padding: 0 20px;
        margin: 0 auto;

        & #state-img-container {
            max-width: 800px;
            margin: 0 auto;
            margin-bottom: 20px;

            & #unlink-btn {
                cursor: pointer;
            }

            & #disconnect-btn {
                cursor: pointer;
            }

            & #tcp-server-4-uids text,
            & #tcp-server-3-uids text,
            & #tcp-server-2-uids text,
            & #tcp-server-1-uids text,
            & #wifi-ip text,
            & #elan-ip text,
            & #rs-485-4-name text,
            & #rs-485-4-client-uids text,
            & #rs-485-3-name text,
            & #rs-485-3-client-uids text,
            & #rs-485-2-name text,
            & #rs-485-2-client-uids text,
            & #rs-485-1-name text,
            & #rs-485-1-client-uids text {
                text-anchor: middle;
            }
        }

        & .state-table-container {
            max-width: 800px;
            margin: 0 auto;

            & #overvis-min-logo,
            & #opcb-device-img-min,
            & #wifi-mini-icon,
            & .usb-icon-mini {
                margin-top: -4px;
            }

            & .bi-wifi {
                font-size: 13px;
                margin-left: 2.4px;
                margin-right: 3.5px;
            }

            & .bi-usb-symbol {
                margin-right: 2px;
            }
            & .bi-usb-symbol:before {
                font-size: 14px;
                margin-left: 2px;
                transform: rotate(180deg);
            }

            & table {
                & .name {
                    color: var(--opcb-bs-useless-text-color);
                }

                & td {
                    padding-left: 0;
                    padding-right: 0;
                }
            }
        }

        & #ac-code-copy-btn-container {
            min-width: 20px;
            min-height: 25px;

            & #ac-code-copy-btn {
                display: block;
            }
            & #ac-code-copied {
                display: none;
            }
            &.copied {
                padding-top: 2px;

                & #ac-code-copy-btn {
                    display: none;
                }

                & #ac-code-copied {
                    color: var(--opcb-bs-green);
                    display: block;
                }
            }
        }

        & #wg-private-key {
            & .text-end {
                display: flex;
                justify-content: end;
                align-items: center;
            }
            & .value {
                display: none;
            }
            & #show-private-key-btn {
                width: 30px;
                & .bi-eye-slash {
                    display: block;
                }
                & .bi-eye-slash {
                    display: none;
                }
            }
            &.show {
                & .value {
                    display: block;
                }
                & #show-private-key-btn {
                    & .bi-eye-slash {
                        display: block;
                    }
                    & .bi-eye {
                        display: none;
                    }
                }
            }
        }
    }

    #actions-container {
        max-width: 1220px;
        padding: 0 20px;
        margin: 0 auto;
    }

    #modbus-container {
        max-width: 1220px;
        padding: 0 20px;
        margin: 0 auto;

        & textarea {
            height: 333px;
            font-family: monospace;
            margin-bottom: 0 !important;
        }

        & #interface-select {
            margin-top: 2.2px;
        }

        /* & #modbus-req-form {
        margin-bottom: 40px;
    } */

        & #bits-container {
            padding-top: 32px;
        }

        & #modbus-values-container {
            & .value-item {
                display: flex;
                justify-content: space-between;
                align-items: baseline;

                & .remove-btn-container {
                    min-width: 20px;
                    & .remove-value-btn {
                        font-size: 15px;
                        color: red;
                    }
                }
            }
        }

        & #add-value-btn {
            font-size: 15px;
        }

        & #responses-container {
            & h4 {
                margin-bottom: 10px;
            }

            & #responses-list {
                /* max-height: 250px; */
                /* min-height: 100px; */
                /* border: 1px solid var(--opcb-bs-grey); */
                /* overflow-y: scroll; */
                font-family: monospace;

                & .response-item {
                    font-size: 12px;
                    margin-bottom: 3px;
                }
            }

            & .timing {
                color: #999;
            }

            & .error {
                color: var(--opcb-bs-red);
            }
        }

        & #clear-log-btn {
            font-size: 12px;
        }

        & #sending-spinner {
            width: 20px;
            height: 20px;
            margin-top: 7px;
            margin-bottom: 7px;
            margin-right: 10px;
            color: #999;
        }
    }

    #manufacturer-container {
        max-width: 1220px;
        padding: 0 20px;
        margin: 0 auto;

        & #test-device-container {
            & #tests-log-container {
                display: none;
            }

            & .test-ok {
                color: #05b859;
            }

            & .test-fail {
                color: var(--opcb-bs-red);
            }

            &.test-run {
                & #run-tests-btn-container {
                    display: none;
                }

                & #tests-log-container {
                    display: block;
                }
            }

            &.finished {
                & #run-tests-spinner {
                    visibility: hidden;
                }
            }
        }
    }

    #quick-start-header {
        margin-bottom: 50px;
    }

    #opcb-quick-setup-container {
        max-width: 1220px;
        padding: 0 20px;
        margin: 0 auto;
        text-align: center;

        & #quick-start-form {
            max-width: 440px;
            margin: 0 auto;

            & .unmain-submit-btn {
                border: 1px solid var(--opcb-bs-blue);
                background-color: none;
                color: var(--opcb-bs-blue);

                &:hover {
                    text-decoration: none;
                    background-color: var(--opcb-bs-grey);
                }
            }
        }

        & #internet-connection-container {
            padding-bottom: 15px;
            margin-bottom: 30px;
            border-bottom: 1px var(--opcb-bs-gray-300) solid;
        }

        & #rs-485-container {
            margin-bottom: 35px;
        }

        &.ethernet {
            & #ethernet-container {
                display: block;
            }
            & #wifi-container {
                display: none;
            }
        }

        &.wifi {
            & #ethernet-container {
                display: none;
            }
            & #wifi-container {
                display: block;
            }
        }

        & #quick-start-internet-methods {
            margin-bottom: 25px;
        }

        & .row {
            text-align: left;
        }
    }

    .select-plus {
        & .plus,
        & .update {
            color: var(--opcb-bs-blue);
            display: flex;
            font-size: 26px;
            cursor: pointer;
            padding: 0;
            border: none;
            background: transparent;
        }

        & .update {
            font-size: 20px;
            font-size: 20px;
            margin-bottom: 4px;
            padding-top: 5px;

            & .spinner-border {
                width: 20px;
                margin-bottom: 4px;
                height: 20px;
            }
        }
    }

    input:not([class*="is-invalid"]).changed {
        border-color: var(--opcb-bs-blue);
    }

    select:not([class*="is-invalid"]).changed {
        border-color: var(--opcb-bs-blue);
    }

    .help-text {
        margin-top: 2px;
        color: var(--opcb-bs-help-text-color);
        font-size: 12px;
    }

    #disconnect-error {
        position: absolute;
        width: 100%;
        height: 100%;
        background-color: #fff;
        opacity: 0.8;
        top: 0;
        display: flex;
        align-items: center;
        justify-content: space-evenly;
        z-index: 1000;

        & h1 {
            color: var(--opcb-bs-dark);
        }
    }

    #reboot-spinner {
        position: absolute;
        width: 100%;
        height: 100%;
        background-color: #fff;
        top: 0;
        display: flex;
        align-items: center;
        justify-content: space-evenly;
        z-index: 1000;
    }

    #load-error {
        position: absolute;
        width: 100%;
        height: 100%;
        background-color: #fff;
        opacity: 0.8;
        top: 0;
        display: flex;
        align-items: center;
        justify-content: space-evenly;
    }

    .spinner-content {
        position: fixed;
        top: 50%;
        display: flex;
        align-items: center;
        flex-direction: column;
        justify-content: center;
    }

    .card {
        border: 0;
        & .card-header {
            background-color: transparent;
            font-weight: bold;
        }
    }

    #opcb-form {
        max-width: 1000px;
    }
}
